
import React  from 'react';
import { Bar,Chart } from "react-chartjs-2";
import data from './data.json'
import ChartDataLabels from 'chartjs-plugin-datalabels';


Chart.register(ChartDataLabels)

//git remote add origin https://github.com/gsc-data/university-pt-access.git
//[[87, 59, 136], [173, 95, 173], [229, 151, 185], [249, 221, 218]]
export const LEGEND_COLORS = {
  15: [87, 59, 136],
  30:[173, 95, 173],
  45: [229, 151, 185],
  60:[249, 221, 218],
};
const BAR_COLORS_BACKGROUND ={};


for (let key in LEGEND_COLORS){
  BAR_COLORS_BACKGROUND[key] = `rgba(${LEGEND_COLORS[key].join()},1)`

};

const BAR_COLORS_FILL ={};

for (let key in LEGEND_COLORS){
  BAR_COLORS_FILL[key] = `rgba(${LEGEND_COLORS[key].join()},0.9)`

};


data.datasets = data.datasets.map( row =>{
  let k = row['label'].split(" ")[0]
  console.log(k);
    return {...row,label:`${row.label} \(%\)`,backgroundColor:BAR_COLORS_FILL[k],borderColor:BAR_COLORS_BACKGROUND[k],borderWidth:1}
});


const options = {
        
  indexAxis: 'y',
  maintainAspectRatio: false,

  

  elements: {
    bar: {
      borderWidth: 1,
    }
  },
  responsive: true,
  
  scales: {
    x: {
        ticks: {
            // Include a dollar sign in the ticks
            callback: function(value, index, values) {
                return   value +'%';
            }
        }
    },
    yAxes: {
      ticks: {
          // Include a dollar sign in the ticks
          // callback: function(value, index, values) {
          //     return `<b>${this.getLabelForValue(value)}</b>`;
          // },
          font: {
           
    
            weight:  function(value){
              const {tick} = value;
              //console.log(tick);
              return tick.label==='Greater Sydney'?"bold":"normal";
            }
     
          },
      }
  }
}


  ,
  plugins: {

    datalabels:{
      formatter: function(value, context) {
        return  value>10? Math.round(value,0) + '%':'';
      },
      anchor:'end',
      align :'start',
      font: {
        size: '8px',
        weight:'bold'
      },
      color: function(context){
        const backgroundColor = context.dataset.backgroundColor
        const [r,g,b] = backgroundColor.slice(5,backgroundColor.length-5).split(',').map(elm=>parseInt(elm))
        const factor =.5;
        //console.log(r);
  
        const newColor= `rgb(${Math.round(r*factor,0)},${Math.round(g*factor,0)},${Math.round(b*factor,0)})`;
        //console.log(newColor);
        return newColor
      }
    },

    legend: {
      position: 'right',
      display: false,
    },
    title: {
      display: false,
      text:` metres access to open space`
    }
  },
  

};





const BarChart = () => {

    
    return ( 
        <div style = {{height:'400px'}}>
        <Bar data = {data} options ={options} />
        </div>

    )

            
            
}
   

export default BarChart;



